"use client"

/**
 * Signup Step 1: profile type selection (Personal vs Business). User picks
 * then continues to residential or business form. Client Component.
 */

import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import { Home, Building2, Check } from "lucide-react"
import { ROUTES } from "@/config"
import { cn } from "@/lib/utils"

type ProfileChoice = "residential" | "business" | null

export default function SignupPage(): JSX.Element {
  const router = useRouter()
  const [choice, setChoice] = useState<ProfileChoice>(null)

  function handleContinue(): void {
    if (choice === "residential") router.push("/signup/residential")
    if (choice === "business") router.push("/signup/business")
  }

  return (
    <>
      <h1 className="font-display text-2xl font-semibold text-text-primary mb-1">
        Create your account
      </h1>
      <p className="text-text-secondary text-base mb-6">
        Who are you shopping for?
      </p>

      <div className="grid grid-cols-2 gap-4 mb-6">
        <button
          type="button"
          onClick={() => setChoice("residential")}
          className={cn(
            "bg-surface-card border rounded-2xl min-h-[140px] flex flex-col items-center justify-center gap-2 relative transition-all",
            choice === "residential"
              ? "border-brand-sage bg-brand-sage/10"
              : "border-surface-border hover:border-brand-sage/40"
          )}
        >
          {choice === "residential" && (
            <span className="absolute top-3 right-3 text-brand-sage" aria-hidden>
              <Check className="w-5 h-5" />
            </span>
          )}
          <Home className="w-8 h-8 text-brand-sage" />
          <span className="font-display font-semibold text-text-primary">
            Personal
          </span>
        </button>
        <button
          type="button"
          onClick={() => setChoice("business")}
          className={cn(
            "bg-surface-card border rounded-2xl min-h-[140px] flex flex-col items-center justify-center gap-2 relative transition-all",
            choice === "business"
              ? "border-brand-sage bg-brand-sage/10"
              : "border-surface-border hover:border-brand-sage/40"
          )}
        >
          {choice === "business" && (
            <span className="absolute top-3 right-3 text-brand-sage" aria-hidden>
              <Check className="w-5 h-5" />
            </span>
          )}
          <Building2 className="w-8 h-8 text-brand-sage" />
          <span className="font-display font-semibold text-text-primary">
            Business
          </span>
        </button>
      </div>

      <button
        type="button"
        onClick={handleContinue}
        disabled={choice === null}
        className="btn-primary w-full min-h-touch"
      >
        Continue
      </button>

      <p className="mt-6 text-center text-text-secondary text-sm">
        Already have an account?{" "}
        <Link href={ROUTES.LOGIN} className="text-brand-sage font-medium hover:underline">
          Sign in
        </Link>
      </p>
    </>
  )
}
